.form {
    max-width: 500px;
    margin: 0 auto;
    padding: 20px;
    border: none;
    border-radius: 10px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  }
  
  .header {
    margin-top: 7%;
    text-align: center;
    margin-bottom: 30px;
    font-size: 30px;
    color: #e4f5ff;
  }
  
  .form-group {
    margin-bottom: 30px;
  }
  
  .form-group label {
    display: block;
    margin-bottom: 5px;
    color: #e4f5ff;
    font-size: 16px;
    font-weight: bold;
  }
  
  .form-group input,
  .form-group textarea {
    width: 100%;
    padding: 10px;
    border: none;
    border-radius: 10px;
    font-size: 16px;
    background-color: #63809c4f;
    color: #e4f5ff;
  }
  
  .form-group textarea {
    resize: none;
    height: 80px;
  }
  
  .form button {
    display: block;
    margin: 0 auto;
    background-color: #007bff;
    color: #e4f5ff;
    border: none;
    border-radius: 10px;
    padding: 12px 50px;
    font-size: 16px;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
  }
  
  .form button:hover {
    background-color: #0069d9;
  }
  
  @media screen and (min-width: 768px) {
    .form {
      max-width: 800px;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-gap: 30px;
      padding: 50px;
    }
  
    .header {
      margin-top: 5%;
    }
  
    .form-group label {
      font-size: 18px;
    }
  
    .form-group input,
    .form-group textarea {
      padding: 12px;
      font-size: 18px;
    }
  
    .form button {
      grid-column: 1 / -1;
      margin: 1rem 12rem;
      font-size: 18px;
    }
  }